body {
  border-radius: border-box;
  background-color: #c1c1c1;
}

.search-form {
  border-bottom: 3px solid #0d8069;
  align-content: center;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 10px;
  display: flex;
}

input {
  width: 80%;
  color: #302f2f;
  letter-spacing: 1px;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #0d8069;
  border-radius: 15px;
  padding: 10px 20px;
  font-family: Epilogue;
  font-size: 30px;
  font-style: normal;
  font-weight: 600;
  line-height: 30px;
  -webkit-text-decoration-color: #0d8069;
  text-decoration-color: #0d8069;
}

input::-ms-input-placeholder {
  color: #0d8069;
  opacity: .6;
}

input::placeholder {
  color: #0d8069;
  opacity: .6;
}

input:hover {
  border: 2px solid #0d8069;
}

button[type="submit"] {
  color: #0d8069;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #0d8069;
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 30px;
}

.submit-btn {
  align-content: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

.submit-btn:hover {
  background-color: rgba(0, 0, 0, 0);
  box-shadow: 1px 4px 6px rgba(0, 0, 0, .16), 0 4px 4px rgba(0, 0, 0, .06), 0 1px 1px rgba(0, 0, 0, .12);
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.article-link {
  text-transform: uppercase;
  color: #fff;
  background-color: rgba(121, 154, 138, .73);
  border-radius: 15px;
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
}

#loadMore {
  cursor: pointer;
  color: var(--dark-green, #0d8069);
  letter-spacing: -.8px;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #0d8069;
  border-radius: 15px;
  padding: 10px 20px;
  font-family: Epilogue;
  font-size: 40px;
  font-style: normal;
  font-weight: 600;
  line-height: 44px;
}

#loadMore:hover {
  background-color: rgba(0, 0, 0, 0);
  box-shadow: 1px 4px 6px rgba(0, 0, 0, .16), 0 4px 4px rgba(0, 0, 0, .06), 0 1px 1px rgba(0, 0, 0, .12);
}

.svg-icon.search-icon {
  width: 40px;
  height: 30px;
  align-content: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

button .svg-icon.search-icon:hover {
  stroke: #299ecc;
  transition: width .5s, height .5s;
}

.svg-icon.search-icon:active {
  stroke: #111516;
  stroke-width: 2px;
}

.info {
  border: 2px solid #0d8069;
  border-radius: 4px;
  flex-direction: row;
  justify-content: space-evenly;
  display: flex;
}

